order-radius: 4px\n    width: fit-content\n    white-space: nowrap\n\n.slider\n  display: flex\n  align-items: center\n  position: relative\n  justify-content: center\n  &:hover &__counter,\n  &:hover &__edges\n    opacity: 1\n    transition: all 2s ease\n\n  &__img\n    padding: 1rem\n    cursor: pointer\n    object-fit: contain\n    z-index: 100\n    width: 250px\n    height: 250px\n    @media screen and (max-width: 425px)\n      width: 100%\n\n  &__id\n    position: absolute\n    top: 0\n    right: 0\n    left: 0\n    bottom: 0\n    display: flex\n    justify-content: center\n    align-items: center\n    z-index: 0\n    font-size: 18rem\n    color: $secondary-color\n    @media screen and (max-width: 720px)\n      font-size: 14rem\n    &--reduce1\n      font-size: 6rem\n      @media screen and (max-width: 720px)\n        font-size: 5rem\n    &--reduce2\n      font-size: 9rem\n      @media screen and (max-width: 720px)\n        font-size: 8rem\n    &--reduce3\n      font-size: 12rem\n      @media screen and (max-width: 720px)\n        font-size: 10rem\n\n  &__counter\n    position: absolute\n    bottom: 0\n    left: 50%\n    transform: translateX(-50%)\n    right: 0\n    background-color: $btn-color2\n    border-radius: 20px\n    padding: 0.2rem 0.8rem\n    cursor: default\n    font-weight: 600\n    width: fit-content\n    border: 1px solid $btn-color3\n    opacity: 0\n    transition: all 1s ease\n    z-index: 100\n    @media screen and (max-width: 720px)\n      opacity: 1\n\n  &__edges\n    position: absolute\n    opacity: 0\n    width: 50%\n    height: 100%\n    display: flex\n    align-items: center\n    justify-content: center\n    cursor: pointer\n    user-select: none\n    transition: all 1s ease\n    z-index: 200\n    @media screen and (max-width: 720px)\n      opacity: 1\n    &:first-of-type\n      justify-content: flex-start\n      left: 0\n    &:last-of-type\n      justify-content: flex-end\n      right: 0\n\n  &__btn\n    font-size: 20px\n    border: non